.front {
	background: -webkit-gradient(linear, left bottom, left top, from(#111), to(#122));
	background: -webkit-linear-gradient(bottom, #111, #122);
	background: -o-linear-gradient(bottom, #111, #122);
	background: linear-gradient(to top, #111, #122);
	display: -ms-flexbox;
	display: flex;
	height: 100vh;
	width: 100%;
}
.banner {
	margin: auto;
}
.front h1 {
	font-size: 70px;
	margin: 0;
	padding: 0;
}
.front p {
	font-size: 20px;
}
.front button {
	letter-spacing: 2px;
	margin: 40px 5px;
	padding-left: 20px; 
	padding-right: 20px; 
	position: relative;
	z-index: 1;
}
.front button:hover {
	letter-spacing: 5px;
	padding-left: 24px; 
	padding-right: 24px; 
}

@media (max-width: 600px) {
	.front {
		padding-top: 18%;
	}
	.front h1 {
		font-size: 50px;
	}
	.front p {
		font-size: 18px;
	}
}
@media (max-width: 450px) {
	.front h1 {
		font-size: 40px;
	}
	.front p {
		font-size: 18px;
	}
}
@media (max-width: 380px) {
	.front h1 {
		font-size: 30px;
	}
	.front p {
		font-size: 14px;
	}
}



