.about {
    height: 100%;
    padding: 3% 0;
    position: relative;
    text-align: left;
}
.about-box {
    margin: 0 auto;
    width: 70%;
}
.about-float {
    float: right;
    padding-left: 10vw;
    text-align: center;
}
.about-L {
    font-size: 30px;
    line-height: 1.5;
    padding-bottom: 10px;
}
.about-S {
    font-size: 22px;
}
.my-img {
    border: 1px solid black;
    background-position: center-top;
    background-size: 140%;
    border-radius: 2px;
    height: 300px;
    margin: 5px;
    margin-top: 10%;
    opacity: 0.9;
    width: 300px;
    -webkit-transform: rotate(10deg) translateY(-40px);
        -ms-transform: rotate(10deg) translateY(-40px);
            transform: rotate(10deg) translateY(-40px);
}

.about-service-container {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: none;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    padding: 0 5vw;
    width: 100%;
}
/*================== service ====================*/
.services-text-box {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin: 0 auto;
    width: 60%;
}
.services-text-box p {
    padding: 0 2%;
    text-align: left;
}
.serviceBox {
    display: inline-block;
    margin-top: 10vh;
    text-align: center;
    width: 20%;
    min-width: 300px;
}
.serviceCircle {
    border-radius: 50%;
    height: 10vw;
    position: relative;
    margin: 0 auto;
    width: 10vw;
    -webkit-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    transition: all 0.3s ease;
}
.icon {
    text-align: center;
    position: relative;
    top: 20%;
    width:60%;
}
.serviceTitle {
    color: #448888;
    font-weight: 500;
    letter-spacing: 1px;
    padding: 10px 0px 30px 0px;
}
.serviceBorder {
    border-bottom: 1px solid #11ffff;
    -webkit-box-shadow: 0px 0px 4px 1px #118888;
            box-shadow: 0px 0px 4px 1px #118888;
    border-radius: 50%;
    margin: 0px 5vw;
}
.serviceText {
    color: silver;
    margin: 0 auto;
    text-align: center;
    padding: 30px 1vw 10vh 1vw;
    width: 70%;
}
.webDes {
    color: #d16455;
}
.webDev {
    color: #714089;
}
.backend {
    color: #1ec503;
}
.socialMedia {
    color: #58b2d8;
}


.dark-bg {
    background: rgba(0, 0, 0, 0.6);
    color: white;
}

@media (max-width: 1000px) {
    .my-img {
        width: 200px;
        height: 200px;
    }
    .about-box {
        width: 70%;
    }
}

@media (max-width: 767px) {
    .my-img {
        height: 150px;
        width: 150px;
    }
    .about-box {
        margin: 6vh auto;
        width: 84%;
    }
    .about-float {
        padding-left: 10px;
    }
    .about-service-container {
        isplay: block;
        -ms-flex-pack: none;
            justify-content: none;
        padding: 0 5vw 0 10vw;
    }
    .about-L {
        font-size: 26px
    }
    .about-S {
        font-size: 20px;
    }

    .serviceBox {
      position: relative;
      margin-top: 20px;
      min-width: none;
      width: 100%;
    }
    .serviceCircle {
      float: left;
      margin-top: 0%;
      text-align: left;
      height: 14vw;
      width: 14vw;
    }
    .serviceTitle {
      float: left;
      padding: 0;
      text-align: left;
      width: 80%;
    }
    .serviceBorder {
      display: none;
    }
    .serviceText {
      margin-right: 4%;
      padding: 0;
      position: relative;
      text-align: left;
      width: 80%;
    }
}



